                                        this.output += '"CLOSE":"CLOSE","E":"'+this.exchange+'","P":"' +this.Producttype+ '","AT":"' + version + '"},<br/>';
                                        this.output += '{"TT":"' + this.Transaction  + '","E":"'+this.exchange+'","Q":"' +this.quantity+ '",' + quantitypercentage + '"OT":"' + this.OrderType + '","P":"' +this.Producttype+ '","VL":"DAY"';
                                        break;
                                    case 'FULLYBO':
                                        this.TP = this.prtp(this.Transaction , this.TP, this.TPpercentage);
                                        this.SL = this.prtp(this.Transaction , this.SL, this.SLpercentage );
                                        this.output += '"CLOSE":"CLOSE","E":"'+this.exchange+'","P":"BO","VL":"DAY","AT":"' + version + '"},<br/>{"TT":"' + this.Transaction  + '","E":"'+this.exchange+'","Q":"' +this.quantity+ '",' + quantitypercentage + '"OT":"' + this.OrderType + '","SL":"' + this.SL + '","TP":"' + this.TP + '","P":"BO","VL":"DAY"';
                                        break;
                                    case 'FULLYCO':
                                        this.SL = this.prtp(this.Transaction , this.SL, this.SLpercentage , '-', '+');
                                        this.output += '"CLOSE":"CLOSE","E":"'+this.exchange+'","P":"CO","VL":"DAY","AT":"' + version + '"},<br/>{"TT":"' + this.Transaction  + '","E":"'+this.exchange+'","Q":"' +this.quantity+ '",' + quantitypercentage + '"OT":"' + this.OrderType + '","LTP":"-0","SL":"' + this.SL + '","P":"CO","VL":"DAY"';
                                        break;
                                    default:
                                        break;
                                }
                                break;
                            case 'CLOSE':
                                this.output += '"CLOSE":"CLOSE"';
                                if (this.tradingsymbol) {
                                    this.output += ',"E":"'+this.exchange+'"';
                                }
                                if (this.quantity) {
                                    this.output += ',"Q":"' +this.quantity+ '"';
                                }
                                if (this.quantitypercentage  && this.PER_LOT_SIZE) {
                                    this.output += ',"PER_LOT_SIZE":"' + this.PER_LOT_SIZE + '"';
                                }
                                if (this.Transaction ) {
                                    this.output += ',"TT":"' + this.Transaction  + '"';
                                }
                                if (this.Producttype) {
                                    this.output += ',"P":"' +this.Producttype+ '"';
                                }
                                break;
                            case 'AS':
                                this.output += '"AS":"' + this.tradingsymbol + '"';
                                if (this.quantity) {
                                    this.output += ',"Q":"' +this.quantity+ '"';
                                }
                                if (this.quantitypercentage  &&  this.PER_LOT_SIZE) {
                                    this.output += ',"PER_LOT_SIZE":"' + this.PER_LOT_SIZE + '"';
                                }
                                if (this.Transaction ) {
                                    this.output += ',"TT":"' + this.Transaction  + '"';
                                }
                                if (this.Producttype) {
                                    this.output += ',"P":"' +this.Producttype+ '"';
                                }
                                //'+quantitypercentage+'

                                break;
                            case 'STRATEGY':
                                this.output += '"TT":"&#123;&#123;strategy.order.action&#125;&#125;","TS":"' + this.tradingsymbol + '","Q":"' +this.quantity+ '",' + quantitypercentage + '"OT":"MARKET",';
                                switch (this.auto_mation) {
                                    case 'FULLYMIS':
                                        this.output += '"P":"' +this.Producttype+ '",';
                                        break;
                                    case 'FULLYBO':
                                        this.TP = this.prtp(this.Transaction , this.TP, this.TPpercentage);
                                        this.SL = this.prtp(this.Transaction , this.SL, this.SLpercentage );
                                        this.output += '"TP":"' + this.TP + '","SL":"' + this.SL + '","P":"BO",';
                                        break;
                                    case 'FULLYCO':
                                        this.SL = this.prtp(this.Transaction , this.SL, this.SLpercentage , '-', '+');
                                        this.output += '"LTP":"-0","SL":"' + this.SL + '","P":"CO",';
                                        break;
                                       // this.output += '"VL":"DAY","PSIZE":"Z&#123;&#123;strategy.position_size&#125;&#125;","STRATEGY":"STRATEGY"';
                                }
                                this.output += '"VL":"DAY","PSIZE":"Z&#123;&#123;strategy.position_size&#125;&#125;","STRATEGY":"STRATEGY"';
                                break;
                            case 'STRIKEPRICE':
                                if (this.Close_without_Value == 'with_close') {
                                    this.output += '"AS":"' + this.exchange + '"';
                                    if (this.auto_mation== "FULLYMIS") {
                                        this.output += ',"P":"' +this.Producttype+ '"';
                                    }
                                    if (this.auto_mation== "FULLYCO") {
                                        this.output += ',"P":"CO"';
                                    }
                                    if (this.auto_mation== "FULLYBO") {
                                        this.output += ',"P":"BO"';
                                    }
                                    this.output += ',"AT":"' + version + '"},<br/>{';
                                }
                                this.output += '"TT":"' + this.Transaction  + '","IT":"' + this.IT + '&#123;&#123;&#123;close&#125;&#125;&#125;' +  this.cal_put + '","Q":"' +this.quantity+ '",' + quantitypercentage + '"OT":"' + this.OrderType + '",';
                                switch (this.auto_mation) {
                                    case 'FULLYMIS':
                                        if (this.OrderType == 'LIMIT') {
                                            this.LTP = this.prtp(this.Transaction , this.LTP,this.LTPpercentage, '-', '+');
                                            this.output += '"LTP":"' + this.LTP + '",';
                                        }
                                        this.output += '"P":"' +this.Producttype+ '",';
                                        break;
                                    case 'FULLYBO':
                                        if (this.OrderType == 'LIMIT') {
                                            this.LTP = this.prtp(this.Transaction , this.LTP,this.LTPpercentage, '-', '+');
                                            this.output += '"LTP":"' + this.LTP + '",';
                                        }
                                        this.TP = this.prtp(this.Transaction , this.TP, this.TPpercentage);
                                        this.SL = this.prtp(this.Transaction , this.SL, this.SLpercentage );
                                        this.output += '"TP":"' + this.TP + '","SL":"' + this.SL + '","P":"BO",';
                                        break;
                                    case 'FULLYCO':
                                        if (this.OrderType == 'LIMIT') {
                                            this.LTP = this.prtp(this.Transaction , this.LTP,this.LTPpercentage, '-', '+');
                                            this.output += '"LTP":"' + this.LTP + '",';
                                        } else {
                                            this.output += '"LTP":"-0",'
                                        }
                                        this.SL = this.prtp(this.Transaction , this.SL, this.SLpercentage , '-', '+');
                                        this.output += '"SL":"' + this.SL + '","P":"CO",';
                                        break;
                                    default:
                                        break;
                                }
                                this.output += '"VL":"DAY","NEAR":"' + this.NEARBY + '","CAL":"' +this.cal_plus_minus  +this.NEARBYCAL + '"';
                                break;
                            default:
    }
     }
     this.output += ',"AT":"'+version+'"}]';
    this.simpleDialog = true;
  },

 prtp(transition_type,value,percentage,opreator1,opreator2) {
      if(!percentage){
        percentage = '';
      }
      if(!opreator1){
        opreator1 = '';
      }
      if(!opreator2){
        opreator2 = '';
      }
        if (transition_type == 'BUY'){ value = opreator1+value+percentage; }
        else{ value = opreator2+value+percentage; }
        return value; 
        },
// Popup
    ShowModal() {
      this.simpleDialog = true;
    },

    copyToClipboard(element) {
var $temp = $("<textarea>");
                var brRegex = /<br\s*[\\]?>/gi;
                $("body").append($temp);
                $temp.val($(element).html().replace(brRegex, "\r\n")).select();
                document.execCommand("copy");
                $temp.remove();
                $('.copy_message').html('SUCCESSFULLY COPY').css({
                    "display": "block",
                    "position": "absolute",
                    "left": "21px",
                    "margin-top": "-3px",
                    "color": "#00a000"
                });}
  },
};
</script>

